<template>
    <view>
        <view v-if="isPushEnd&&bodyData.content==0"
            style="text-align: center;"
        >
        <view style="height: 80rpx;"></view>
        <view>
            <image
                class="compass-icon"
                :src="$kt.file.byPath('/image/icon/compass.svg')"
                mode="widthFix"
                style="width: 200rpx;"
            />
        </view>
        <view style="height: 20rpx;"></view>
        <view style="color:#999;font-size:28rpx">
            没有找到附近的人
        </view>

        </view>

        <view v-for="(item, index) in  bodyData.content" :style="{
            'opacity': item.opacity
        }">
            <user-card 
            @click="$refs.userPopup.open(item)"
            :ovoUserInfo="item"></user-card>
            <view style="height: 10rpx;"></view>
        </view>
        <view style="height: 50rpx;" v-if="bodyData.length == 0"></view>
        <!-- 正在获取用户信息 -->
        <view v-if="isPushing">
            <view style="height: 30rpx;"></view>
            <view style="text-align: center;">
                <image :src="$kt.file.byPath('kantboot/icon/loading.svg')" class="loading-icon" mode="widthFix"></image>
            </view>
        </view>

        <view
        v-if="isPushFinall&&!isPushEnd&&bodyData.content.length>7"
        style="padding: 30rpx 0 30rpx 0;text-align: center;color: #999;font-size: 30rpx;"
        >下滑可能获取得到更多推荐~</view>
        <view
        v-if="isPushFinall&&!isPushEnd&&bodyData.content.length>7"
        style="height: 40rpx;"></view>

        <view v-if="isPushFinall&&isPushEnd&&pushEndShow&&bodyData.content.length>7"
        style="padding: 30rpx 0 30rpx 0;text-align: center;color: #999;font-size: 30rpx;"
        >
            获取不到更多了，一分钟后再试试吧~
        </view>
        <view v-if="isPushFinall&&isPushEnd&&!pushEndShow&&bodyData.content!=0"
        style="padding: 30rpx 0 30rpx 0;text-align: center;color: #999;font-size: 30rpx;"
        >
        没有更多了~
        </view>


        <user-popup ref="userPopup"></user-popup>

</view>
</template>

<script>

export default {
    data() {
        return {
            isPushing: false,
            // 是否添加完成
            isPushFinall:true,
            isPushEnd:false,
            pushEndShow:true,
            bodyData: {
                content:[],
                totalElements:0
            },
            ovoSelf: {
                userId: ""
            },
            requestParam: {
                pageNumber: 1,
				range:"200000"
				// range:"99999999999"
            }
        }
    },
    created() {
        this.ovoSelf = this.$ovo.getSelf();
        this.getBodyData();
        uni.$on("pageIndexOnScrollToLower",()=>{
            if(this.isPushFinall){
                this.requestParam.pageNumber++;
                this.getBodyData();
            }
        })
        
        uni.$on("loginSuccess", ()=>{
            this.isPushFinall=true;
            this.isPushing = false;
            this.isPushEnd=false;
            this.getBodyData();
        });
    },
    methods: {
        itemClick(res) {
        },
        async getBodyData() {
            if(!this.isPushFinall||this.isPushing||this.isPushEnd){
                if(this.pushEndShow){
                    setTimeout(()=>{
                        this.pushEndShow=false;
                    },1000);
                    setTimeout(()=>{
                        this.pushEndShow=true;
                    },60000);
                }

                return false;
            }

            this.isPushFinall=false;
            this.isPushing = true;
            var jsonArr = [];
            var resData=null;
            await new Promise((resolve, reject) => {
                this.$kt.request.request({
                    api: this.$api.business.ovo.userBind.getNear,
                    data: this.requestParam,
                    stateSuccess: (res) => {
                        jsonArr = res.data.content;
                        resData = res.data;
                        resolve();
                    }
                })
            });


            if(this.bodyData.content.length>=resData.totalElements||resData.content.length==0){
                this.isPushEnd=true;
                setTimeout(()=>{
                    this.isPushEnd=false;
                },60000);

                setTimeout(()=>{
                    this.isPushing = false;
                },500);
                setTimeout(()=>{
                    this.isPushFinall=true;
                },500);
                this.requestParam.pageNumber--;
                return false;
            }
            this.isPushing = false;

            // 如果jsonArr中有userId和bodyData的userId相同，则不添加
            for (var i = 0; i < jsonArr.length; i++) {
                var isExist = false;
                for (var j = 0; j < this.bodyData.content.length; j++) {
                    if (jsonArr[i].userId == this.bodyData.content[j].userId) {
                        isExist = true;
                        break;
                    }
                }
                if (!isExist) {
                    if(jsonArr[i].userId==this.ovoSelf.userId){
                        i++;
                    }
                    
                    var json = { ...jsonArr[i], 'opacity': 0 };

                    this.bodyData.content.push(json);


                    await new Promise((resolve, reject) => {

                        var interVal = setInterval(() => {
                            this.bodyData.content[this.bodyData.content.length - 1].opacity += 0.1;
                            this.$forceUpdate();
                            if (this.bodyData.content[this.bodyData.content.length - 1].opacity >= 1) {
                                clearInterval(interVal);
                                resolve("")
                            }
                        }, 50);

                    });
                }
            }
            this.isPushFinall=true;


        }
    }
}

</script>

<style lang="scss" scoped>
.loading-icon {
    width: 70rpx;
    height: 70rpx;
    filter: invert(90%);
    // 不停旋转的动画
    animation: rotate 3s linear infinite;
}

@keyframes rotate {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.compass-icon {
    width: 70rpx;
    height: 70rpx;
    filter: invert(60%);
    // 左右摇摆的动画
    animation: swing 3s linear infinite;
}

@keyframes swing {
    0% {
        transform: rotate(0deg);
    }

    25% {
        transform: rotate(90deg);
    }

    50% {
        transform: rotate(0deg);
    }

    75% {
        transform: rotate(-90deg);
    }


    100% {
        transform: rotate(0deg);
    }
}
</style>